import React, { Fragment } from "react";

const Info = () => {
  // DOM elements with text which shows when instructions are needed
  return (
    <Fragment>
      <div className="content">
        <p>Bst resultat kan uppns nr ansiktet r ca 40 cm frn kameran.</p>
        <p>Se till att det r bra ljus och att kameran r ren.</p>
        <p>Sitt stilla och rr inte p huvudet.</p>
        <p>
          Titta rakt in i kameran eller p den rda prickan verst och ta en
          bild.
        </p>
        <p>
          Resultatet till vnster r det ungefrliga avstndet mellan pupiller.
        </p>
        <p>Resultatet till hger r PD-medelvrdet om du tar flera bilder.</p>
        <p>
          Du kan ta om bilden s mnga gnger du vill (vnta tills videon laddas
          igen).
        </p>
      </div>
    </Fragment>
  );
};

export default Info;
